






















































import { Component, Vue } from 'vue-property-decorator';
import { storage } from '@/common/utils';
import { LsData } from '@/common/types';
import { format } from 'date-fns';

@Component
export default class App extends Vue {
  siteData: LsData[] = [];

  created(): void {
    this.siteData = storage.load();
  }

  formatDate(date: string): string {
    return format(new Date(date), 'HH:mm:ss EEEEEE do LLL yyyy');
  }

  handleForm(e: Event): void {
    const targ = e.target as HTMLFormElement;
    const txtArea = targ.querySelector('textarea') as HTMLTextAreaElement;
    const progressDiv = targ.querySelector('#form-progress') as HTMLElement;

    const data: LsData = {
      content: txtArea.value,
      storedOn: new Date()
    };

    const storedData = storage.load();
    storedData.push(data);
    storage.save(storedData);

    this.siteData.push(data);

    txtArea.value = '';
    progressDiv.textContent = 'Success!';

    setTimeout(() => {
      progressDiv.textContent = '';
    }, 3000);
  }

  deleteNote(datum: LsData): void {
    if (
      window.confirm(
        `Are you sure you want to delete this note?\nContent: "${
          datum.content
        }"\nCreated on: ${this.formatDate(datum.storedOn.toString())}`
      )
    ) {
      let storedData = storage.load();
      storedData = storedData.filter((item) => item.content !== datum.content);
      this.siteData = storedData;
      storage.save(storedData);
    }
  }

  deleteAllNotes(): void {
    if (window.confirm('Are you sure you want to delete all notes?')) {
      this.siteData = [];
      storage.reset();
    }
  }
}
